.Balloon {
    background-image: url("./scroll.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
    border-radius: 15px;
}

@media (max-width: 768px) {
    .Balloon {
        width: 100%;
        max-width: 125px;
        height: auto;
    }
    .img {
        max-width: 100%;
    }
}
